import React, { Component } from 'react';
import Modal from 'react-modal';
import Colors from './components/Colors'
import DetailsThumb from './components/DetailsThumb';
import './App.css';
import Image1 from './assets/image/7b719b33aa5d5e03074c.jpg';
import Image2 from './assets/image/26850c243d4ac914905b.jpg';
import Image3 from './assets/image/943560b751d9a587fcc8.jpg';
import Image4 from './assets/image/d028ea4edb202f7e7631.jpg';
import Video1 from './assets/video/bo-thai-doc-caruso-quick-clean.mp4';

class App extends Component {
  state = {
    modalIsOpen: false,
    products: [
      {
        "_id": "1",
        "title": "Thải Độc & Làm Sạch Nội Tạng 15 Ngày Carusos Natural Health Quick Cleanse 15 Day Detox Program",
        "src": [
            Image1,
            Image1,
            Image2,
            Image3,
            Image4
          ],
        "description": "Quick Cleanse Carusos Detox Là bộ sản phẩm chuyên dùng để detox thải độc được tin tưởng nhất ở ÚC vì cơ chế thải độc sử dụng các thành phần tự nhiên, ngoài ra còn bổ sung PROBIOTICS (lợi khuẩn) cho cơ thể. Để có 1 sức khỏe tốt thì nên thải độc detox ít nhất 1 năm 1 lần.",
        "content": "Quick Cleanse chứa 25tỷ lợi khuẩn giúp giảm đau bụng, táo bón, đầy hơi, tiêu chảy, buồn nôn, hơi thở hôi, dị ứng, khó tiêu, ợ nóng, viêm da, rối loạn tiêu hóa nhẹ hoặc nghèo khả năng miễn dịch đường ruột bạn. Quick Cleanse với các sợi tự nhiên và thảo dược như 1 cái chổi đường ruột giúp làm sạch các bức tường bên trong của ruột, cho phép một lượng lớn chất thải dễ dàng đưa ra khỏi cơ thể.",
        "price": 1680000,
        "discount": 2420000,
        "brand": "Caruso’s Natural Health Úc",
        "specifications": "Liệu Trình 15 ngày",
        "origin": "Mua Trực Tiếp Tại Chemist Warehouse Úc",
        "colors":["red","black","crimson","teal"],
        "count": 1
      }
    ],
    index: 0,
    email: '',
    feedback: 'Test', 
    name: '',
    phoneNumber: '',
  };

  myRef = React.createRef();

  handleTab = index =>{
    this.setState({index: index})
    const images = this.myRef.current.children;
    for(let i=0; i<images.length; i++){
      images[i].className = images[i].className.replace("active", "");
    }
    images[index].className = "active";
  };

  componentDidMount(){
    const {index} = this.state;
    this.myRef.current.children[index].className = "active";
  }

  openModal = () => {
    this.setState({ modalIsOpen: true });
  };

  closeModal = () => {
    this.setState({ modalIsOpen: false });
  };

  handleSubmit = (event) => {
    window.emailjs.send(
      "service_c4hnuvu", "template_vmilezr",
      {
        from_name: "HaNguyenQuang",
        to_name: "HaiNguyenQuang",
        message: "Họ và tên: " + this.state.name + ", " + "Email: " + this.state.email + ", " + "Số điện thoại: " + this.state.phoneNumber,
      },
      "user_H6ukGjWJ9Rx5YGWttv3Ew",
      ).then(res => {
        console.log('Email successfully sent!')
      })
      // Handle errors here however you like, or use a React error boundary
      .catch(err => console.error('Oh well, you failed. Here some thoughts on the error that occured:', err))
  }

  handeChangeName = (name) => {
    this.setState({name: name});
  }

  handeChangeEmail = (email) => {
    this.setState({email: email});
  }

  handeChangePhoneNumber = (phoneNumber) => {
    this.setState({phoneNumber: phoneNumber});
  }

  render() {
    const {products, index} = this.state;
    return (
      <div className="app">
        {
          products.map(item =>(
            <div className="details" key={item._id}>
              {
                this.state.index === 0 ? 
                (
                  <video className="video1" width="400" height="400" controls>
                    <source src={Video1} type="video/mp4" />
                  </video>
                ) : (
                  <div className="big-img">
                    <img src={item.src[index]} alt=""/>
                  </div>
                )
              }
              <div className="box">
                <div className="row">
                  <h2>{item.title}</h2>
                  <div>
                    <span className="discount">₫{item.discount.toLocaleString()}</span>
                    <span className="price">₫{item.price.toLocaleString()}</span>
                  </div>
                  
                </div>
                <p>
                  Hãng sản xuất: {item.brand}
                </p>
                <p>
                  Quy cách: {item.specifications}
                </p>
                <p>
                  Xuất xứ: {item.origin}
                </p>
                <Colors colors={item.colors} />

                <p>{item.description}</p>
                <p>{item.content}</p>
                {
                  this.state.index === 0 ? 
                  (
                    <video className="video2" width="400" height="400" controls>
                      <source src={Video1} type="video/mp4" />
                    </video>
                  ) : (
                    <div className="big-img2">
                      <img src={item.src[index]} alt=""/>
                    </div>
                  )
                }
                <DetailsThumb images={item.src} tab={this.handleTab} myRef={this.myRef} />                
                <div className="buttonCart">
                  <button onClick={this.openModal} className="cart">Đặt hàng</button>
                </div>
              </div>
            </div>
          ))
        }
        <Modal isOpen={this.state.modalIsOpen} onRequestClose={this.closeModal}>
          <div className="formModal">
            <div className="titleForm">Đăng ký nhận thông tin sản phẩm</div>
            <div className="rowForm">
              <div className="label">Họ và tên<div className="reddot">*</div>:</div>
              <input className="inputForm" type="text" name="user_name" onChange={(e) => this.handeChangeName(e.target.value)} placeholder="Nhập họ và tên"/>
            </div>
            <div className="rowForm">
              <div className="label">Email<div className="reddot">*</div>:</div>
              <input className="inputForm" type="text" name="email" onChange={(e) => this.handeChangeEmail(e.target.value)} placeholder="Nhập Email liên hệ"/>
            </div>
            <div className="rowForm">
              <div className="label">Số điện thoại liên hệ<div className="reddot">*</div>: </div>
              <input className="inputForm" type="text" name="phone_number" onChange={(e) => this.handeChangePhoneNumber(e.target.value)} placeholder="Nhập Số điện thoại liên hệ"/>
            </div>
            <div className="rowFormRequie"><div className="reddot">*</div>Yêu cầu nhập thông tin.</div>
          </div>
          <div className="footerForm">
            <button className="buttonCancel" onClick={this.closeModal}>Đóng</button>
            {
              this.state.email && this.state.name && this.state.phoneNumber ? (
                <button className="buttonSubmit" onClick={this.handleSubmit}>Xác nhận đặt</button>
              ) : (
                <div className="buttonSubmitDisable">Xác nhận đặt</div>
              )
            }
          </div>
        </Modal>
      </div>
    );
  }
}

export default App;
